import React, { useState, useEffect } from "react";

function Header({
  scrollToHome,
  scrollToAbout,
  scrollToProjects,
  scrollToContact,
  theme,
}) {
  const [menuOpen, setMenuOpen] = useState(false);
  const [activeSection, setActiveSection] = useState("home");

  const toggleMenu = () => {
    setMenuOpen(!menuOpen);
  };

  const handleScroll = () => {
    const sections = document.querySelectorAll("section");
    let currentSection = "";

    sections.forEach((section) => {
      const sectionTop = section.offsetTop;
      const sectionHeight = section.offsetHeight;
      if (
        window.pageYOffset >= sectionTop - 60 &&
        window.pageYOffset < sectionTop + sectionHeight - 60
      ) {
        currentSection = section.getAttribute("id");
      }
    });

    setActiveSection(currentSection || "home");
  };

  useEffect(() => {
    window.addEventListener("scroll", handleScroll);
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, []);

  const headerClasses = `fixed top-5 right-10 p-4 ${
    theme === "dark"
      ? "bg-gray-800 text-white border-gray-700"
      : "bg-white text-gray-900 border-gray-200"
  } border-2 rounded-lg shadow-lg transition-colors duration-300 z-50`;

  const linkClasses = `text-lg relative hover:text-blue-500 transition-colors duration-300`;
  const activeLinkClasses = `${linkClasses} after:content-[''] after:absolute after:w-full after:h-0.5 after:bg-blue-500 after:bottom-[-5px] after:left-0 after:rounded-full`;

  return (
    <header className={headerClasses}>
      <div
        className={`lg:hidden cursor-pointer ${menuOpen ? "open" : ""}`}
        onClick={toggleMenu}
      >
        <div
          className={`w-6 h-0.5 mb-1.5 ${
            theme === "dark" ? "bg-white" : "bg-gray-900"
          } transition-all duration-300 ${
            menuOpen ? "transform rotate-45 translate-y-2" : ""
          }`}
        ></div>
        <div
          className={`w-6 h-0.5 mb-1.5 ${
            theme === "dark" ? "bg-white" : "bg-gray-900"
          } transition-all duration-300 ${menuOpen ? "opacity-0" : ""}`}
        ></div>
        <div
          className={`w-6 h-0.5 ${
            theme === "dark" ? "bg-white" : "bg-gray-900"
          } transition-all duration-300 ${
            menuOpen ? "transform -rotate-45 -translate-y-2" : ""
          }`}
        ></div>
      </div>
      <nav className={`${menuOpen ? "block" : "hidden"} lg:block`}>
        <ul className="flex flex-col lg:flex-row items-center space-y-4 lg:space-y-0 lg:space-x-6">
          <li>
            <a
              href="#home"
              onClick={() => {
                scrollToHome();
                setMenuOpen(false);
              }}
              className={
                activeSection === "home" ? activeLinkClasses : linkClasses
              }
            >
              Home
            </a>
          </li>
          <li>
            <a
              href="#about"
              onClick={() => {
                scrollToAbout();
                setMenuOpen(false);
              }}
              className={
                activeSection === "about" ? activeLinkClasses : linkClasses
              }
            >
              About
            </a>
          </li>
          <li>
            <a
              href="#projects"
              onClick={() => {
                scrollToProjects();
                setMenuOpen(false);
              }}
              className={
                activeSection === "projects" ? activeLinkClasses : linkClasses
              }
            >
              Projects
            </a>
          </li>
          <li className="lg:after:content-['|'] lg:after:ml-6">
            <a
              href="#contact"
              onClick={() => {
                scrollToContact();
                setMenuOpen(false);
              }}
              className={
                activeSection === "contact" ? activeLinkClasses : linkClasses
              }
            >
              Contact
            </a>
          </li>
          <li>
            <a href="/" className="hover:text-gray-300">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="24"
                height="24"
                viewBox="0 0 24 24"
                fill="none"
                stroke="currentColor"
                strokeWidth="2"
                strokeLinecap="round"
                strokeLinejoin="round"
              >
                <path d="M9 19c-5 1.5-5-2.5-7-3m14 6v-3.87a3.37 3.37 0 0 0-.94-2.61c3.14-.35 6.44-1.54 6.44-7A5.44 5.44 0 0 0 20 4.77 5.07 5.07 0 0 0 19.91 1S18.73.65 16 2.48a13.38 13.38 0 0 0-7 0C6.27.65 5.09 1 5.09 1A5.07 5.07 0 0 0 5 4.77a5.44 5.44 0 0 0-1.5 3.78c0 5.42 3.3 6.61 6.44 7A3.37 3.37 0 0 0 9 18.13V22"></path>
              </svg>
            </a>
          </li>
          <li>
            <a href="/" className="hover:text-gray-300">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="24"
                height="24"
                viewBox="0 0 24 24"
                fill="none"
                stroke="currentColor"
                strokeWidth="2"
                strokeLinecap="round"
                strokeLinejoin="round"
              >
                <path d="M4 4h16c1.1 0 2 .9 2 2v12c0 1.1-.9 2-2 2H4c-1.1 0-2-.9-2-2V6c0-1.1.9-2 2-2z"></path>
                <polyline points="22,6 12,13 2,6"></polyline>
              </svg>
            </a>
          </li>
        </ul>
      </nav>
    </header>
  );
}

export default Header;
